@import "../../../utilities/mixins/clickable";

@mixin base() {
    border: none;
    border-radius: 0.3rem;
    display: block;
    padding-left: 2rem;
    padding-right: 2rem;
    text-align: center;
    text-transform: uppercase;
}

&-button {
    @include base;
    @include clickable($clickable-default);

    &--primary {
        @include base;
        @include clickable($clickable-primary);
    }

    &--positive {
        @include base;
        @include clickable($clickable-positive);
    }

    &--negative {
        @include base;
        @include clickable($clickable-negative);
    }
}