@mixin base() {
    @include set-text-style($text-style-4);

    @include breakpoint(medium) {
        display: none;
    }

    color: $color-link-inverse;
    position: absolute;
    top: 0;
    right: 0;
    transform: translateY(50%);

    &:hover {
        color: $color-link-inverse;
        text-decoration: none;
    }
}

&__toggle-link {
    @include base;
}